<template>
  <div id="main" style="width:500px;height:500px;"></div>
</template>

<script>
import echarts from "echarts";
export default {
  data() {
    return {
      picBase64Info:null
    }
  },
  mounted() {
    // 基于准备好的dom，初始化echarts实例
    var myChart = echarts.init(this.$el);
    // 绘制图表
    myChart.setOption({
      title: {
        text: "ECharts 入门示例"
      },
      animation: false, //取消动画导出base64
      tooltip: {},
      xAxis: {
        data: ["衬衫", "羊毛衫", "雪纺衫", "裤子", "高跟鞋", "袜子"]
      },
      yAxis: {},
      series: [
        {
          name: "销量",
          type: "bar",
          data: [5, 20, 36, 10, 10, 20]
        }
      ]
    });
    this.picBase64Info = myChart.getDataURL(); //导出base64
  }
};
</script>

